.post {
    margin-bottom: 10px;
    border-radius: 10px;
    padding: 15px;
    background-color: white;
}

.post__header {
    display: flex;
    margin-bottom: 10px;
}

.post__info {
    margin-left: 10px;
}
.post__info > p{
    font-size: 12px;
    color: gray;
}
.post__body {
    overflow-wrap: anywhere;
}
.post__buttons {
    display: flex;
    justify-content: space-evenly;
}